<template>
    <div class="collection">
        <van-nav-bar
            title="我的收藏"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
        />

        <div class="movie-box">
            <div class="content-box clearfix" v-for="(item,index) in collectionData" :key="index" @click="movieDetail({name: 'movieDetail', params: {id: item.id}})">
                <div class="img fl">
                    <img class="auto-img" :src="item.images.medium" alt="">
                </div>

                <div class="content fl">
                    <div class="title movie-name">{{item.title}}</div>
                    <div class="title movie-people">主演:{{item.avatarsInfo}}</div>
                    <div class="title movie-type">类别:{{item.genresInfo}}</div>
                    <div class="title movie-time">时间:{{item.durations[0]}}</div>
                </div>

                <div class="buy-info fl">
                    <div class="number">{{item.rating.average}}分</div>

                    <div class="delect" @click.stop="delect(item)">删除</div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name:'collection',
        data(){
            return {

                collectionData:[]
            }
        },
        created(){
            let collectionData = localStorage.getItem('collectionData');
            collectionData = collectionData ? JSON.parse(collectionData) : [];
            this.collectionData = collectionData

        },

        methods:{
            onClickLeft(){
                this.$router.go(-1)
            },

            movieDetail(o){
                this.$router.push(o)
            },
            delect(item){


                let collectionData = localStorage.getItem('collectionData');
                collectionData = collectionData ? JSON.parse(collectionData) : [];
                this.collectionData.forEach(v => {
                    if(item.id === v.id){
                        this.collectionData.splice(v,1)
                    }
                });
                localStorage.setItem('collectionData',JSON.stringify(this.collectionData));

            }


            
        }
        
    }
</script>

<style lang="less" scoped>
.collection{
    .movie-box{    
        position: absolute;
        top: 46px;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: #f5f5f5;
        overflow-y: auto;

        .content-box{
            padding: 10px 0;
            overflow: hidden;
            background-color: #fff;
            margin-bottom: 10px;
        }

        .img{
            width: 80px;
            height: 120px;
            padding: 0 10px;
        }

        .content{
            width: 170px;
            height: 120px;
            padding: 0 10px;
        }
        .title{
            font-size: 12px;
            max-width: 170px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 15px;
        }
        .movie-name{
            font-size: 18px;
            font-weight: bold;
            
        }
        .movie-people{
            color: #659D0E;
        }

        .buy-info{
            width: calc(~"100% - 290px - 20px");
            height: 120px;
            padding: 0 10px;
        }
        .number{
            font-size: 16px;
            color: red;
            text-align: center;
            line-height: 16px;;
        }
        .delect{
            width: 30px;
            height: 20px;
            border: 1px solid #000;
            margin-top: 40px;
            float: right;
            text-align: center;
            line-height: 20px;
        }
    }
}

</style>